import React from 'react';
import PropTypes from 'prop-types';

class SocialButton extends React.Component {
  render() {
    let position;
    switch(this.props.position) {
      case('left'):
      case('pull-left'):
        position = 'pull-left';
        break;
      case('right'):
      case('pull-right'):
        position = 'pull-right';
        break;
      default:
        position = '';
    }
    let buttonClass, buttonName;
    if (this.props.type === 'like') {
      buttonClass = "fa-thumbs-o-up";
      buttonName = "Like";
    } else if(this.props.type === 'share') {
      buttonClass = "fa-share";
      buttonName = "Share";
    }
    return (
      <button className={`btn btn-xs ${position} ${this.props.theme}`}>
        <i className={`fa ${buttonClass}`} />
        {buttonName}
      </button>
    );
  }
}
SocialButton.propTypes = {
  position: PropTypes.string,
  theme: PropTypes.string,
  type: PropTypes.string
};
SocialButton.defaultProps = {
  position: 'left',
  theme: 'btn-default',
  type: 'like'
};

export default SocialButton;
